	module.exports = {"data":{"markdownRemark":{"fileAbsolutePath":"/home/luanorlandi/git/angeloocana/src/pages/blog/linux/arch/setup-nodejs-env.fr.md","html":"<h2>Install git</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">sudo</span> pacman -S <span class=\"token function\">git</span>\n</code></pre>\n      </div>\n<h2>Install Nodejs</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">sudo</span> pacman -S nodejs\n</code></pre>\n      </div>\n<h2>Install NPM</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">sudo</span> pacman -S <span class=\"token function\">npm</span>\n</code></pre>\n      </div>\n<h3>Fix global permissions error</h3>\n<p><a href=\"https://docs.npmjs.com/getting-started/fixing-npm-permissions\">https://docs.npmjs.com/getting-started/fixing-npm-permissions</a></p>\n<p>Make a directory for global installations:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">mkdir</span> ~/.npm-global\n</code></pre>\n      </div>\n<p>Configure npm to use the new directory path:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">npm</span> config <span class=\"token keyword\">set</span> prefix <span class=\"token string\">'~/.npm-global'</span>\n</code></pre>\n      </div>\n<p>Open or create a ~/.profile file and add this line:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">export</span> PATH<span class=\"token operator\">=</span>~/.npm-global/bin:<span class=\"token variable\">$PATH</span>\n</code></pre>\n      </div>\n<p>Back on the command line, update your system variables:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">source</span> ~/.profile\n</code></pre>\n      </div>\n<h2>Install vs code</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    yaourt -S visual-studio-code\n</code></pre>\n      </div>\n<h2>Install Python for Gatsby</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">sudo</span> pacman -S python2\n</code></pre>\n      </div>","excerpt":"Install git Install Nodejs Install NPM Fix global permissions error https://docs.npmjs.com/getting-started/fixing-npm-permissions Make a…","fields":{"tagSlugs":[{"tag":"Linux","link":"/fr/tags/linux/"},{"tag":"Arch","link":"/fr/tags/arch/"},{"tag":"NodeJs","link":"/fr/tags/node-js/"},{"tag":"Dev env","link":"/fr/tags/dev-env/"},{"tag":"Npm","link":"/fr/tags/npm/"},{"tag":"Git","link":"/fr/tags/git/"},{"tag":"Vs code","link":"/fr/tags/vs-code/"}],"slug":"/fr/blog/linux/arch/setup-nodejs-env/","readNextPosts":[{"excerpt":"J’adore les films français, ils sont aussi proches de la vie réelle,\nde vraies personnes.\nIls parlent de sexe, de drogues et d’autres tabous…","frontmatter":{"title":"Films français","date":"2017-08-08"},"fields":{"slug":"/fr/blog/languages/french/french-movies/","langKey":"fr"}},{"excerpt":"Cette erreur se produit lorsque vous regardez de nombreux fichiers dans linux. Vous pouvez recevoir un message d’erreur comme ceci: error…","frontmatter":{"title":"Correction de l'erreur ENOSPC","date":"2017-09-03"},"fields":{"slug":"/fr/blog/linux/arch/fix-enospc-error/","langKey":"fr"}},{"excerpt":"","frontmatter":{"title":"Exemplos Programação Funcional","date":"2017-06-23"},"fields":{"slug":"/fr/blog/functional-programming/examples/","langKey":"fr"}}]},"frontmatter":{"youtubeId":null,"title":"Setup dev env Arch linux","tags":["Linux","Arch","NodeJs","Dev env","Npm","Git","Vs code"],"date":"2017-07-30","structuredData":null}}},"pathContext":{"path":"/fr/blog/linux/arch/setup-nodejs-env/","langKey":"fr"}}

